body {
  font-family: "Roboto", sans-serif;
  font-size: 15px;
  line-height: 1.8;
  color: #090909;
}

input:focus:required:invalid,
textarea:focus:required:invalid {
  border: 1px solid #E53A40;
}

input:focus:required:valid,
textarea:focus:required:valid {
  border: 1px solid #0DA20E;
}

*::-webkit-input-placeholder,
*:-moz-placeholder,
*::-moz-placeholder,
*:-ms-input-placeholder {
  color: #6f6f6f;
  opacity: 1;
}

.goto-top {
  position: fixed;
  bottom: 53px;
  right: -50px;
  transition: transform ease-out .5s;
}

.goto-top--show {
  transform: translateX(-70px);
}

.goto-top__btn {
  display: block;
  width: 50px;
  height: 50px;
  background: #f5a44c;
  border-radius: 50%;
  text-align: center;
  line-height: 50px;
  color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);
  transition: background ease-out 0.3s, color ease 0.3s;
}

.goto-top__btn:hover {
  background: #cf720c;
  color: #fff;
}

.goto-top__btn:focus {
  color: #fff;
}

.goto-top__btn:active {
  background: #fad5ad;
}